import PageTemplate from "../../components/PageTemplate/PageTemplate";
import "./PublishingUrlPage.scss";

const PublishingUrlPage = () => {
  //   -     
  const urlData = [
    {
      id: 1,
      depth1: "",
      depth2: "",
      depth3: "",
      depth4: "",
      url: "https://example.com",
      description: " "
    },
    {
      id: 2,
      depth1: "",
      depth2: "",
      depth3: "",
      depth4: "",
      url: "https://example.com/menu/coffee/americano",
      description: "  "
    },
    {
      id: 3,
      depth1: "",
      depth2: "",
      depth3: "",
      depth4: "",
      url: "https://example.com/menu/coffee/latte/vanilla-latte",
      description: "   "
    },
    {
      id: 4,
      depth1: "",
      depth2: "",
      depth3: "",
      depth4: "",
      url: "https://example.com/store/locator",
      description: "  "
    },
    {
      id: 5,
      depth1: "",
      depth2: "",
      depth3: "",
      depth4: "",
      url: "https://example.com/membership/tiers/gold",
      description: "   "
    }
  ];

  return (
    <PageTemplate title=" URL ">
      <div className="publishing-url-page">
        <div className="publishing-url-page__table-container">
          <table className="publishing-url-table">
            <thead>
              <tr>
                <th>1</th>
                <th>2</th>
                <th>3</th>
                <th>4</th>
                <th>URL </th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {urlData.map((item) => (
                <tr key={item.id}>
                  <td>{item.depth1 || "-"}</td>
                  <td>{item.depth2 || "-"}</td>
                  <td>{item.depth3 || "-"}</td>
                  <td>{item.depth4 || "-"}</td>
                  <td>
                    <a href={item.url} target="_blank" rel="noopener noreferrer">
                      {item.url}
                    </a>
                  </td>
                  <td>{item.description}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </PageTemplate>
  );
};

export default PublishingUrlPage;
