import { useState } from "react";
import "./FileUpload.scss";

const MAX_SIZE = 300 * 1024 * 1024; // 300MB
const allowedTypes = ["application/pdf"];

const isAllowed = (file) => file.type.startsWith("image/") || allowedTypes.includes(file.type);

const formatSize = (bytes) => {
  if (bytes >= 1024 * 1024 * 1024) return `${(bytes / (1024 * 1024 * 1024)).toFixed(1)} GB`;
  if (bytes >= 1024 * 1024) return `${(bytes / (1024 * 1024)).toFixed(1)} MB`;
  if (bytes >= 1024) return `${(bytes / 1024).toFixed(1)} KB`;
  return `${bytes} B`;
};

function FileUpload() {
  const [fileInfo, setFileInfo] = useState(null);

  const handleClear = () => {
    setFileInfo(null);
    const input = document.getElementById("component-file-input");
    if (input) input.value = "";
  };

  const handleFileChange = (event) => {
    const file = event.target.files?.[0];
    if (!file) return;

    if (file.size > MAX_SIZE) {
      alert(" 300MB   .");
      event.target.value = "";
      return;
    }

    if (!isAllowed(file)) {
      alert("  . (: , PDF)");
      event.target.value = "";
      return;
    }

    setFileInfo({
      name: file.name,
      size: file.size,
      type: file.type,
    });
  };

  return (
    <div className="file-upload-demo">
      <div className="file-upload-demo__field">
        <label className="file-upload-demo__label" htmlFor="component-file-input">
           
        </label>
        <input
          id="component-file-input"
          type="file"
          accept="image/*,application/pdf"
          onChange={handleFileChange}
        />
        <p className="file-upload-demo__hint">
            300MB&nbsp;&nbsp; : , PDF&nbsp;&nbsp;    
        </p>
      </div>

      <div className="file-upload-demo__status">
        {fileInfo ? (
          <>
            <div className="file-upload-demo__status-head">
              <p className="file-upload-demo__status-title"> </p>
              <button
                type="button"
                className="file-upload-demo__clear"
                onClick={handleClear}
                aria-label="  "
              >
                
              </button>
            </div>
            <ul>
              <li><strong>:</strong> {fileInfo.name}</li>
              <li><strong>:</strong> {formatSize(fileInfo.size)}</li>
              <li><strong>:</strong> {fileInfo.type}</li>
            </ul>
          </>
        ) : (
          <p className="file-upload-demo__placeholder">  .</p>
        )}
      </div>
    </div>
  );
}

export default FileUpload;

