import { useState } from "react";
import "./Tabs.scss";

function Tabs() {
  const tabItems = [
    { id: "detail", label: "", description: " , ,    ." },
    { id: "review", label: "", description: "   / ." },
    { id: "qa", label: "Q&A", description: "         ." },
  ];
  const [activeTab, setActiveTab] = useState(tabItems[0].id);
  const activeItem = tabItems.find((item) => item.id === activeTab);

  return (
    <div className="tabs-demo">
      <div className="tabs-demo__tablist" role="tablist" aria-label="  ">
        {tabItems.map((item) => (
          <button
            key={item.id}
            type="button"
            role="tab"
            aria-selected={activeTab === item.id}
            onClick={() => setActiveTab(item.id)}
          >
            {item.label}
          </button>
        ))}
      </div>
      <div
        className="tabs-demo__tabpanel"
        role="tabpanel"
        aria-live="polite"
        aria-label={`${activeItem?.label}  `}
      >
        {activeItem?.description}
      </div>
    </div>
  );
}

export default Tabs;

